@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: inherit;
  text-decoration: inherit;
}

.App{
  /* background: rebeccapurple; */
  max-width: 100vw;
}
.page {
  padding: 0 5%;
  display: flex;
  min-height: 88vh;
  margin-top: 12vh;
  /* overflow-x: hidden; */
}

.header {
  background: white;
  height: 10vh;
  position: fixed;
  top: 0;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid lightgrey;
  z-index: 100;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.logo-section {
  padding: 0 2%;
  /* background: lavenderblush; */
  height: 60%;
}

.logo {
  height: 100%;
}

.navlinks-section {
  padding: 0 2%;
  height: 100%;
  display: flex;
  align-items: center;
  /* color: white; */
}

.navlink {
  /* display: flex; */
  /* align-items: center; */
  /* background: lavenderblush; */
  margin: 0 8px;
  font-size: 16px;
  transition-duration: 0.4s;
  /* height: 100%; */
}
.navlink:hover {
  color: #85b14a;
  /* text-decoration: underline; */
}

.activenavlink {
  /* text-decoration: underline; */
  transition-duration: 0.4s;
  color: #3a7d42;
  /* color: red; */
}
.activenavlink:hover {
  color: #3a7d42;
  text-decoration: none;
}

.homepage {
  /* background: lavenderblush; */
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
}

.hero-section {
  height: 100vh;
  width: 100vw;
  /* background: lavenderblush; */
  /* background: wheat; */
  margin-top: -2vh;
}

.slider {
  height: 100%;
  width: 100%;
  display: flex;
  /* flex-direction: column; */
}

.each-slide {
  height: 70vh;
  display: flex;
  width: 100%;
  /* flex-direction: column; */
}

.each-slide-image {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  /* justify-content: center; */
  color: white;
  transition-duration: 0.4s;
  opacity: 0.9;
}

.each-slide-image:hover {
  opacity: 1;
}

.each-slide-image-info {
  margin: 50px;
  background: black;
  min-height: 50px;
  padding: 10px 20px;
  min-width: 240px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.each-slide-image-info-title {
  font-weight: 500;
}

.each-slide-image-info-location {
  font-size: 12px;
}

.each-slide-image-info-learnmore {
  font-size: 10px;
  margin: 4px;
  width: 100%;
  text-align: right;
  fill: white;
}

.each-slide-image-info-learnmore-icon{
    height: 12px;
    width: 12px;
}
.error-page {
  /* background: lightcyan; */
  display: flex;
  width: 100vw;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.error-code{
/* font-size: 72px; */
height: 25vh;
}

.error-page-back-button{
    background: #adcd4e;
    /* color: white; */
    border: 1px solid #adcd4e;
    outline: none;
    font-weight: 500;
    /* border: none; */
    margin: 35px 0;
    cursor: pointer;
    padding: 10px;
    transition-duration: 0.4s;
}
.error-page-back-button:hover{
    color:#3a7d42;
    background: white;
}
